.list {
  padding-left: 0;
  display: flex;
  flex-direction: column;
  font-weight: 500;
  font-size: 16px;
}

.item {
  height: 30px;
  list-style-type: none;
  margin: 0;
  padding: 0;
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.btn {
  margin: 5px;
  width: 80px;
  height: 20px;
}
